@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
#Home {
  display: grid;
  grid-template-columns: 1fr 570px;
  margin: 0 100px;
  grid-gap: 100px;
  gap: 100px;
}

#Head {
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  height: 100vh;
  top: 0;
}

#Head img {
  max-height: 60px;
  margin-bottom: 20px;
}

#Head nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#Head li a {
  display: block;
  font: 400 12px 'Inter', sans-serif;
  color: var(--secondary-color);
  letter-spacing: 3px;
  margin-top: 15px;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: 300ms;
}

#Head li a hr {
  display: inline-block;
  width: 30px;
  border: 0;
  height: 0;
  border-top: 1px solid var(--secondary-color);
  margin-right: 10px;
  vertical-align: middle;
  transition: 300ms;
}

#Head nav ul li:hover a,
#Head nav ul li .active {
  color: var(--primary-color);
}

#Head nav ul li:hover hr,
#Head nav ul li .active hr {
  border-color: var(--primary-color);
  width: 60px;
}

#Head .social {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  margin-top: 20px;
}

#Head .social a {
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 3px;
  text-align: center;
}

#Head .social a:hover {
  opacity: 0.6;
}

#Head .social img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#Hero {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Hero .content {
  text-align: center;
}

#Hero .memoji {
  max-height: 300px;
}

#Hero .arrow-down {
  max-height: 15px;
  cursor: pointer;
  -webkit-animation: floating 1s infinite alternate;
  animation: floating 1s infinite alternate;
}

@-webkit-keyframes floating {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

#Resume {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Skills {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Portfolio {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Articles {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Contact {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#Foot {
  text-align: center;
}

:root {
  --background-color: #151515;
  --primary-color: #ffffff;
  --secondary-color: #949495;
  --tertiary-color: #f2c94c;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  background: #151515;
  background: var(--background-color);
  color: #ffffff;
  color: var(--primary-color);
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
}

h1,
h2,
h3 {
  font: 500 1em 'Barlow', sans-serif;
  line-height: 1em;
}

h1 {
  font-size: 3em;
  margin-bottom: 10px;
}

p {
  font: 400 1em 'Inter', sans-serif;
  line-height: 1.6em;
  color: #949495;
  color: var(--secondary-color);
}

a {
  color: #ffffff;
  color: var(--primary-color);
}

.sub-header {
  font-size: 12px;
  text-transform: uppercase;
  color: #f2c94c;
  color: var(--tertiary-color);
  letter-spacing: 3px;
  margin-bottom: 5px;
}

select {
  background: #202022;
  height: 31px;
  border: none;
  border-radius: 3px;
  padding: 0 10px;
  font-size: 12px;
  text-transform: uppercase;
  color: #949495;
  color: var(--secondary-color);
  letter-spacing: 3px;
  -o-appearance: none;
  -ms-appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

#Head select:-ms-expand {
  display: none;
}

